<template lang="html">
  <div>
    <code-box id="components-input-demo-addon" title="/" :code="code" :description="description">
      <div slot="instance">
        <h3></h3>
        <ant-input type="textarea" placeholder=""></ant-input>
        <div style="margin: 24px 0;"></div>
        <ant-input type="textarea" placeholder="" :autosize="{ minRows: 2, maxRows: 6 }"></ant-input>
      </div>
    </code-box>
  </div>
</template>

<script>
import { template as description } from './autosize-textarea.md'

export default {
  data: () => ({
    description,
    code: `
      <div>
        <ant-input type="textarea" placeholder="" autosize></ant-input>
        <div style="margin: 24px 0;"></div>
        <ant-input type="textarea" placeholder="" :autosize="{ minRows: 2, maxRows: 6 }"></ant-input>
      </div>
    `
  })
}
</script>



