<template lang="html">
  <div>
    <code-box id="components-alert-demo-icon" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-alert message="" type="success" show-icon></ant-alert>
        <ant-alert message="" type="info" show-icon></ant-alert>
        <ant-alert message="" type="warning" show-icon></ant-alert>
        <ant-alert message="" type="error" show-icon></ant-alert>
        <ant-alert message=""
          description=""
          type="success"
          show-icon
        ></ant-alert>
        <ant-alert message=""
          description=""
          type="info"
          show-icon
        ></ant-alert>
        <ant-alert
          message=""
          description=""
          type="warning"
          show-icon
        ></ant-alert>
        <ant-alert
          message=""
          description=""
          type="error"
          show-icon
        ></ant-alert>
      </div>
    </code-box>
  </div>
</template>

<script>
import { template as description } from './closable.md'

export default {
  data: () => ({
    description,
    code: `
      <template lang="html">
        <div>
          <ant-alert message=""
            type="warning"
            closable
            :on-close="onClose"
          ></ant-alert>
          <ant-alert message=""
            description=""
            type="error"
            closable
            :on-close="onClose"
          ></ant-alert>
        </div>
      </template>

      \<script\>
      export default {
        methods: {
          onClose (e) {
            console.log(e, '')
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    onClose (e) {
      console.log(e, '')
    }
  }
}
</script>



