<template lang="html">
  <div>
    <code-box id="components-alert-demo-basic" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-alert message=""
          type="warning"
          closable
          :on-close="onClose"
        ></ant-alert>
        <ant-alert message=""
          description=""
          type="error"
          closable
          :on-close="onClose"
        ></ant-alert>
      </div>
    </code-box>
  </div>
</template>

<script>
import { template as description } from './closable.md'

export default {
  data: () => ({
    description,
    code: `
      <template lang="html">
        <div>
          <ant-alert message=""
            type="warning"
            closable
            :on-close="onClose"
          ></ant-alert>
          <ant-alert message=""
            description=""
            type="error"
            closable
            :on-close="onClose"
          ></ant-alert>
        </div>
      </template>

      \<script\>
      export default {
        methods: {
          onClose (e) {
            console.log(e, '')
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    onClose (e) {
      console.log(e, '')
    }
  }
}
</script>



